@import "../../../css/module/color";
@import "../../../css/module/fn";



#ulBuyerList {
	width: p2m(700);
	height: p2m(235);
	border-radius: p2m(8);
	margin: 20px auto;
	
	li {
		height: p2m(128);
		line-height: p2m(128);
		font-size: p2m(28);
		padding: 0 p2m(50);
		@include shadow();
		background-color: #fff;
		
		a:active {
			color: $red;
		}
	}
}